import React, { useContext, useState } from "react";
import { VehicleContext } from "../VehiclePolicy";
import {
  FormGroup,
  FormControlLabel,
  Checkbox,
  FormControl,
  Select,
  MenuItem,
  Typography,
  TextField,
  Divider,
  Button,
} from "@mui/material";

export default function PolicyPremium() {
  const {
    state,
    handleInputChange,
    handleDateChange,
    data,
    tplCompuation,
    comComputation,
    isAddOrEditMode,
  } = useContext(VehicleContext);
  const [text, setText] = useState(
    JSON.parse(state.MortgageeForm)
      ? `LOSS and/or DAMAGE, if any under this policy shall be payable to ${state.Mortgagee} as their interest may appear subject to all terms and conditions, clauses and warranties of this policy. SUBJECT TO THE ATTACHED STANDARD ACCESSORIES ENDORSEMENT CLAUSE; FULL PREMIUM PAYMENT IN CASE OF LOSS CLAUSE; MEMORANDUM ON DOCUMENTARY STAMPS TAX; ANTI CARNAPING; PREVENTION TIPS AND AUTO PA RIDER; DRUNKEN AND DRIVE CLAUSE THIS POLICY OR ANY RENEWAL THEREOF SHALL NOT BE CANCELLED WITHOUT PRIOR WRITTEN NOTIFICATION AND CONFORMIY TO ${state.Mortgagee}`
      : "SUBJECT TO THE ATTACHED STANDARD ACCESSORIES ENDORSEMENT CLAUSE; FULL PREMIUM PAYMENT IN CASE OF LOSS CLAUSE; MEMORANDUM ON DOCUMENTARY STAMPS TAX; ANTI CARNAPING PREVENTION TIPS AND AUTO PA RIDER; DRUNKEN AND DRIVER CLAUSE"
  );

  return (
    <div>
      <fieldset
        style={{
          display: "flex",
          columnGap: "10px",
          padding: "15px",
          border: "1px solid #cbd5e1",
          borderRadius: "5px",
        }}
      >
        <legend>Mortgagee</legend>
        <div style={{ display: "flex", columnGap: "10px", flex: 1 }}>
          <div>
            <FormGroup>
              <FormControlLabel
                disabled={isAddOrEditMode}
                control={
                  <Checkbox
                    name="MortgageeForm"
                    value={JSON.parse(state.MortgageeForm)}
                    checked={JSON.parse(state.MortgageeForm)}
                    onChange={(e) => {
                      if (e.target.checked) {
                        setText(
                          `LOSS and/or DAMAGE, if any under this policy shall be payable to ${state.Mortgagee} as their interest may appear subject to all terms and conditions, clauses and warranties of this policy. SUBJECT TO THE ATTACHED STANDARD ACCESSORIES ENDORSEMENT CLAUSE; FULL PREMIUM PAYMENT IN CASE OF LOSS CLAUSE; MEMORANDUM ON DOCUMENTARY STAMPS TAX; ANTI CARNAPING; PREVENTION TIPS AND AUTO PA RIDER; DRUNKEN AND DRIVE CLAUSE THIS POLICY OR ANY RENEWAL THEREOF SHALL NOT BE CANCELLED WITHOUT PRIOR WRITTEN NOTIFICATION AND CONFORMIY TO ${state.Mortgagee}`
                        );
                      } else {
                        setText(
                          "SUBJECT TO THE ATTACHED STANDARD ACCESSORIES ENDORSEMENT CLAUSE; FULL PREMIUM PAYMENT IN CASE OF LOSS CLAUSE; MEMORANDUM ON DOCUMENTARY STAMPS TAX; ANTI CARNAPING PREVENTION TIPS AND AUTO PA RIDER; DRUNKEN AND DRIVER CLAUSE"
                        );
                      }

                      handleDateChange(`${e.target.checked}`, "MortgageeForm");
                    }}
                  />
                }
                label="MortgageeForm"
              />
            </FormGroup>
            <Typography variant="body1">Form and Endorcement</Typography>
          </div>
          <div
            style={{
              flex: 1,
              display: "flex",
              flexDirection: "column",
              rowGap: "10px",
            }}
          >
            <FormControl
              size="small"
              fullWidth
              required
              disabled={isAddOrEditMode}
            >
              <Select
                labelId="Mortgagee"
                value={state.Mortgagee}
                name="Mortgagee"
                onChange={handleInputChange}
              >
                {[
                  { type: "" },
                  ...data.data.vehiclePolicy.mortgagee[
                    `${state.form_type.toLowerCase()}`
                  ],
                ].map((items: any, idx: number) => {
                  return (
                    <MenuItem key={idx} value={items.Mortgagee}>
                      {items.Mortgagee}
                    </MenuItem>
                  );
                })}
              </Select>
            </FormControl>
            <div
              style={{
                border: "1px solid #cbd5e1",
                height: "100%",
                padding: "5px",
                overflowY: "auto",
                maxHeight: "350px",
              }}
            >
              <Typography variant="caption">{text}</Typography>
            </div>
          </div>
        </div>
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            rowGap: "10px",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
          }}
        >
          <legend>Premiums</legend>
          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              padding: "5px",
            }}
          >
            <Button
              disabled={isAddOrEditMode}
              size="small"
              variant="contained"
              color="primary"
              onClick={() => {
                if (state.form_type.toLowerCase() === "com") {
                  return comComputation();
                }
                tplCompuation();
              }}
            >
              Compute
            </Button>
          </div>
          {state.form_type.toLowerCase() === "tpl" && (
            <TextField
              disabled={isAddOrEditMode}
              required
              fullWidth
              variant="outlined"
              size="small"
              label="Section I/II"
              name="SectionI_II"
              value={state.SectionI_II}
              onChange={(e) => {
                handleInputChange(e);
              }}
            />
          )}
          {state.form_type.toLowerCase() === "com" && (
            <React.Fragment>
              <div
                style={{
                  display: "grid",
                  gridTemplateColumns: "repeat(2,1fr)",
                  gap: "10px",
                }}
              >
                <TextField
                  disabled={isAddOrEditMode}
                  required
                  fullWidth
                  variant="outlined"
                  size="small"
                  label="Section III(%)"
                  name="SectionIII"
                  value={state.SectionIII}
                  onChange={handleInputChange}
                />
                <TextField
                  disabled={isAddOrEditMode}
                  required
                  fullWidth
                  variant="outlined"
                  size="small"
                  label="Own Damage"
                  name="OwnDamage"
                  value={state.OwnDamage}
                  onChange={handleInputChange}
                />
                <TextField
                  disabled={isAddOrEditMode}
                  required
                  fullWidth
                  variant="outlined"
                  size="small"
                  label="Theft"
                  name="Theft"
                  value={state.Theft}
                  onChange={handleInputChange}
                />
                <TextField
                  disabled={isAddOrEditMode}
                  required
                  fullWidth
                  variant="outlined"
                  size="small"
                  label="Section IV-A"
                  name="SectionIVA"
                  value={state.SectionIVA}
                  onChange={handleInputChange}
                />
                <TextField
                  disabled={isAddOrEditMode}
                  required
                  fullWidth
                  variant="outlined"
                  size="small"
                  label="Section IV-B"
                  name="SectionIVB"
                  value={state.SectionIVB}
                  onChange={handleInputChange}
                />
                <TextField
                  disabled={isAddOrEditMode}
                  label="Other"
                  required
                  variant="outlined"
                  size="small"
                  name="PremiumOther"
                  value={state.PremiumOther}
                  onChange={handleInputChange}
                />
              </div>

              <div
                style={{
                  display: "flex",
                  gap: "10px",
                }}
              >
                <TextField
                  disabled={isAddOrEditMode}
                  required
                  fullWidth
                  variant="outlined"
                  size="small"
                  label="AOG Percent"
                  name="AOGPercent"
                  value={state.AOGPercent}
                  onChange={handleInputChange}
                />
                <p
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    width: "30px",
                    height: "3px",
                    backgroundColor: "black",
                  }}
                ></p>

                <TextField
                  disabled={isAddOrEditMode}
                  required
                  fullWidth
                  variant="outlined"
                  size="small"
                  label="AOG"
                  name="AOG"
                  value={state.AOG}
                  onChange={handleInputChange}
                />
              </div>
            </React.Fragment>
          )}
          <Divider color="secondary" />

          <div
            style={{
              display: "grid",
              gridTemplateColumns: "repeat(2,1fr)",
              gap: "10px",
            }}
          >
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Total Premium"
              name="TotalPremium"
              value={state.TotalPremium}
              onChange={(e) => {
                handleInputChange(e);
              }}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Vat"
              name="Vat"
              value={state.Vat}
              onChange={(e) => {
                handleInputChange(e);
              }}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Doc Stamp"
              name="DocStamp"
              value={state.DocStamp}
              onChange={(e) => {
                handleInputChange(e);
              }}
            />
            {state.form_type.toLowerCase() === "com" ? (
              <div
                style={{
                  display: "flex",
                  gap: "10px",
                }}
              >
                <TextField
                  disabled={isAddOrEditMode}
                  required
                  variant="outlined"
                  size="small"
                  label="Local Gov Tax Percetage"
                  name="LocalGovTaxPercent"
                  value={state.LocalGovTaxPercent}
                  onChange={(e) => {
                    handleInputChange(e);
                  }}
                />
                <p
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    width: "30px",
                    height: "3px",
                    backgroundColor: "black",
                  }}
                ></p>

                <TextField
                  disabled={isAddOrEditMode}
                  required
                  variant="outlined"
                  size="small"
                  label="Local Gov Tax"
                  name="LocalGovTax"
                  value={state.LocalGovTax}
                  onChange={(e) => {
                    handleInputChange(e);
                  }}
                />
              </div>
            ) : (
              <TextField
                disabled={isAddOrEditMode}
                required
                variant="outlined"
                size="small"
                label="Local Gov Tax"
                name="LocalGovTax"
                value={state.LocalGovTax}
                onChange={(e) => {
                  handleInputChange(e);
                }}
              />
            )}
          </div>

          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="StradCom"
            name="StradCom"
            value={state.StradCom}
            onChange={(e) => {
              handleInputChange(e);
            }}
          />
          <Divider color="secondary" />

          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Total Due"
            name="TotalDue"
            value={state.TotalDue}
            onChange={handleInputChange}
          />
        </fieldset>
      </fieldset>
    </div>
  );
}
